        deletePoll: async (context, payload) => {
            try{
                const url =  opentext.mainurl + '/api/v1/nodes/' + payload.id;
                const response = await axios.delete(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                context.commit('deletePoll', payload);
                console.log(response);
            }catch(err){
                console.log(err);
            } 
        },
        getPolls: async (context) => {
            try{
                const url =  opentext.mainurl + getApis().getChildrenElements + '&parentid=' + opentext.cicCommunityPoll;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                const data = JSON.parse(response.data.data).result;
                data.pop();
                context.state.polls = data;
            }catch(err){
                console.log(err);
            } 
        },
