        createChannel: async (context, payload) => {
            const myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            const formdata = new FormData();
            formdata.append("type", "207");
            formdata.append("parent_id", opentext.cicChannels);
            formdata.append("name", payload.name);
        
            const requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };
            try{
                const response = await fetch(opentext.mainurl + "/api/v1/nodes", requestOptions);
                if (!response.ok) // or check for response.status
                    throw new Error(response.statusText);
                console.log(response);
            }catch(error){
                return 'Error';
            }
        },
        createNews: async (context, payload) => {
            const myHeaders = new Headers();
            myHeaders.append("OTCSTicket", opentext.OTCSTicket);

            const formdata = new FormData();
            formdata.append("type", "208");
            formdata.append("parent_id", payload.channelId);
            formdata.append("name", payload.name);

            if(payload.description){
                formdata.append("description", payload.description);
            }
            if(payload.image_id){
                formdata.append("image_id", payload.image_id);
            }
            if(payload.image_alt_text){
                formdata.append("image_alt_text", payload.image_alt_text);
            }
            if(payload.effective_date){
                formdata.append("effective_date", payload.effective_date);
            }
            if(payload.expiration_date){
                formdata.append("expiration_date", payload.expiration_date);
            }
            if(payload.headline){
                formdata.append("headline", payload.headline);
            }
            if(payload.highlights){
                formdata.append("highlights", payload.highlights);
            }
            if(payload.story){
                formdata.append("story", payload.story);
            }
            if(payload.attachments){
                formdata.append("attachments", payload.attachments);
            }
        
            const requestOptions = {
              method: 'POST',
              headers: myHeaders,
              body: formdata,
              redirect: 'follow'
            };
            try{
                const response = await fetch(opentext.mainurl + "/api/v1/nodes", requestOptions);
                if (!response.ok) // or check for response.status
                    throw new Error(response.statusText);
                console.log(response);
            }catch(error){
                return 'Error';
            }
        }
