import axios from 'axios';
export default {
    namespaced: true,
    state: () => ({
        events: []
    }),
    getters: {
        events: (state) => state.events
    },
    mutations: {
    },
    actions: {
        addEvent: async (context, payload) => {
            let details = {
                'showQuickEvent':  'false',
                'tzOffset': '0',
                'func': 'CommCalendar.AddQuickEvent',
                'objId': opentext.cicCommunity,
                'nextURL':  opentext.nextUrl,
                'eventTitle': payload.title,
                'startDate': payload.startDate,
                'endDate': payload.endDate
            };
            if(payload.allDayEvent){
                details['allDayEvent'] = 'TRUE';
            }
            let formBody = [];
            for (var property in details) {
              const encodedKey = encodeURIComponent(property);
              const encodedValue = encodeURIComponent(details[property]);
              formBody.push(encodedKey + "=" + encodedValue);
            }
            formBody = formBody.join("&");
            const response = await fetch(opentext.mainurl, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
              },
              body: formBody
            });
            await context.dispatch('getEvents');
        },
        updateEvent: async (context, payload) => {
            let func = '';
            if(payload.isDelete){
                func = 'CommCalendar.DeleteAppt'
            }else{
                func = 'CommCalendar.EditAppt2';
            }
            let details = {
                'func': func,
                'objId': opentext.cicCommunity,
                'nextURL':  opentext.nextUrl,
                'title': payload.title,
                'NewStartDate': payload.startDate,
                'NewEndDate': payload.endDate,
                'active_lst': '{}',
                'calendarID': payload.id
            }; 
            if(payload.allDayEvent){
                details['allDayEvent'] = 'true';
                details['allDayEvent2'] = 'TRUE';
            }else{
                details['allDayEvent'] = 'false';
            }  
            if(payload.location){
                details['location'] = payload.location;
            }else{
                details['location'] = '';
            }
            if(payload.details){
                details['details'] = payload.details;
            }else{
                details['details'] = '';
            }
            let formBody = [];
            for (var property in details) {
              const encodedKey = encodeURIComponent(property);
              const encodedValue = encodeURIComponent(details[property]);
              formBody.push(encodedKey + "=" + encodedValue);
            }
            formBody = formBody.join("&");
            const response = await fetch(opentext.mainurl, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
              },
              body: formBody
            });
            await context.dispatch('getEvents');
        },
        getEvents: async (context) => {
            try{
                const url =  opentext.mainurl + getApis().getEvents + '&calendarId='+ opentext.cicCommunity;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket
                }});
                const data = JSON.parse(response.data.data).result;
                data.pop();
                context.state.events = data;
              }catch(err){
                  console.log(err);
              } 
        }
    }
}