import axios from 'axios';
export default {
    namespaced: true,
    state: () => ({
      blogHeadlines: [],
      vacancyHeadlines: [],
      vacancies: []
    }),
    getters: {
      blogHeadlines: (state) => state.blogHeadlines,
      vacancyHeadlines: (state) => state.vacancyHeadlines,
      vacancies: (state) => state.vacancies
    },
    mutations: {
    },
    actions: {
        createBlog: async(context, payload) => {
            const id = await getWebCacheId();
            const details = {
                        'func': 'll',
                        'objType': '357',
                        'objAction': 'wizard2',
                        'parentId': '-' + opentext.hrBlog,
                        'nextURL': opentext.nextUrl,
                        "stepID": '1',
                        "cacheID": id,
                        "secureRequestToken": opentext.secureToken,
                        'attDeleteID': '',
                        'attDeleteType': '',
                        'name': payload.name,
                        'comment': '',
                        'imgFolderID': opentext.ImagesFolderOfZBlog,
                        '_journal_blogContent': payload.content,
                        '_journal_blogPublish': 'on',
                        'InheritRequired': 0,
                        'CREATE_Required': 1,
                        'CREATE_Edited': 0,
                        'CREATE_CacheID': 0,
                        'CREATE_CatNames': '',
                        'CTT_ID': '-' + opentext.hrBlog,
                        'CTT_PathSaved': 'Z',
                        'ATT_ID': 0,
                        'ATT_PathSaved': '',
                        'ATT_Path': '',
                        'URL_NAME': '',
                        'URL_URL': 'http%3A%2F%2F',
                        'nextStepID': '-1'
            };
            let formBody = [];
            for (var property in details) {
              const encodedKey = encodeURIComponent(property);
              const encodedValue = encodeURIComponent(details[property]);
              formBody.push(encodedKey + "=" + encodedValue);
            }
            formBody = formBody.join("&");
            const response = await fetch(opentext.mainurl, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
              },
              body: formBody
            })
        },
        getBlogHeadlines: async(context) => {
          try{
            const url =  opentext.mainurl + getApis().getBlogHeadline + '&blogId='+ opentext.hrBlog;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket
            }});
            const data = JSON.parse(response.data.data).result;
            data.pop();
            context.state.blogHeadlines = data;
          }catch(err){
              console.log(err);
          } 
        },
        getVacancyHeadlines: async(context) => {
          try{
            const url =  opentext.mainurl + getApis().getBlogHeadline + '&blogId='+ opentext.hrBlog;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket
            }});
            const data = JSON.parse(response.data.data).result;
            data.pop();
            context.state.blogHeadlines = data;
          }catch(err){
              console.log(err);
          } 
        },
        getVacancies: async (context, payload) => {
          try{
              const url =  opentext.mainurl + getApis().getNews + '&parentId=' + payload.parentId;
              const response = await axios.get(url, { headers: { 
                  'OTCSTicket': opentext.OTCSTicket, 
              }});
              const data = JSON.parse(response.data.data).result;
              data.pop();
              context.state.vacancies = data.slice();
          }catch(err){
              console.log(err);
          } 
      }
    }
}