<template>
    <div>
        <chat-window
            :current-user-id="currentUserId"
            :rooms="rooms"
            :rooms-loaded="isRoomsLoaded"
            :messages-loaded="isMessagesLoaded"
            :messages="messages"
            :message-actions = "messageActions"
            @delete-message = "deleteMessageAction"
            @add-room="showRoom"
            @fetch-messages = 'getRoomMessages'
            @send-message = 'sendMsg'
            @typing-message = 'typingMessage'
            height="85vh"
        />
        <v-dialog
            v-model="newRoom"
            max-width="600px"
            @click:outside="close"
        >
            <v-card>
                <v-card-title>
                    Users
                </v-card-title>
                <v-card-text>
                    <v-container v-if="isUsersList">
                        <v-row>
                            <v-col>
                                <v-list flat>
                                     <v-list-item-group>
                                        <v-list-item v-for="(item, i) in members" :key="i" @click="selectUser(item)">
                                            <v-list-item-content>{{item.name_formatted}}</v-list-item-content>
                                        </v-list-item>
                                    </v-list-item-group>
                                </v-list>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-container v-if="!isUsersList">
                        <v-row>
                            <v-col>
                                <v-text-field
                                  label="Limit"
                                  outlined
                                  v-model="limit"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col>
                                <v-text-field
                                  label="Axtar"
                                  outlined
                                  v-model="query"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions class="justify-center">
                    <v-btn text @click="findUser" v-if="!isUsersList">Axtar</v-btn>
                    <v-btn text @click="returnToFindUsers" v-if="isUsersList">Geriyə</v-btn>
                    <v-btn text @click="addRoom" v-if="isUsersList">Əlavə et</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import ChatWindow from 'vue-advanced-chat';
import 'vue-advanced-chat/dist/vue-advanced-chat.css'
import { mapActions, mapGetters } from 'vuex';
export default {
    components: {
        ChatWindow
    },
    computed: {
        ...mapGetters('member', ['members'])
    },
    data() {
        return{
            newRoom: false,
            messageActions: [
              {
                name: 'replyMessage',
                title: 'Reply'
              },
              {
                name: 'deleteMessage',
                title: 'Delete Message',
                onlyMe: true
              }
            ],
            rooms: [
                {
                  roomId: 1,
                  roomName: 'Room 1',
                  avatar: '',
                  unreadCount: 15,
                  index: 3,
                  lastMessage: {
                    content: 'Last message received',
                    senderId: 1234,
                    username: 'John Doe',
                    timestamp: '10:20',
                    saved: true,
                    distributed: false,
                    seen: false,
                    new: true
                  },
                  users: [
                    {
                      _id: 1234,
                      username: 'John Doe',
                      avatar: '',
                      status: {
                        state: 'online',
                        lastChanged: 'today, 14:30'
                      }
                    },
                    {
                      _id: 4321,
                      username: 'John Snow',
                      avatar: '',
                      status: {
                        state: 'offline',
                        lastChanged: '14 July, 20:00'
                      }
                    }
                  ],
                  typingUsers: [ 4321 ]
                }
            ],
            isRoomsLoaded: true,
            isMessagesLoaded: false,
            messages: [],
            messagesFromServer: [
                {
                  _id: 7890,
                  content: 'hi',
                  senderId: 1234,
                  username: 'John Doe',
                  avatar: '',
                  date: '13 November 2018',
                  timestamp: '10:20',
                  saved: true,
                  seen: true,
                },
                {
                  _id: 7891,
                  content: ' hi',
                  senderId: 4321,
                  username: 'John Snow',
                  avatar: '',
                  date: '13 November 2019',
                  timestamp: '10:21',
                  saved: true,
                  seen: true,
                  disableReactions: true
                },
                {
                  _id: 7892,
                  content: 'how are you?',
                  senderId: 1234,
                  username: 'John Doe',
                  avatar: '',
                  date: '13 November 2020',
                  timestamp: '10:20',
                  disableReactions: true
                },

            ],
            currentUserId: 1234,
            limit: '',
            query: '',
            isUsersList: false,
            selectedUser: {}
        }
    },
    methods: {
        ...mapActions('member', [
            'getMembers'
        ]),
        ...mapActions('chat', [
            'sendMessage'
        ]),
        findUser: async function(){
           let payload = {
               limit: this.limit,
               query: this.query
           }
           await this.getMembers(payload);
           this.isUsersList = true;
        },
        showRoom: function(){
             this.newRoom = true;
        },
        addRoom: function(){

        },
        returnToFindUsers: function(){
            this.isUsersList = false;
        },
        close: function(){
            this.limit = '';
            this.query = '';
        },
        getRoomMessages: function({room}){
            this.messages = this.messagesFromServer;
            this.isMessagesLoaded = true;
        },
        sendMsg: async function({ roomId, content, file, replyMessage, usersTag }){
            var last = this.messages[this.messages.length-1];
            var message = {
                  _id: last._id+1,
                  content: content,
                  senderId: 1234,
                  username: 'John Doe',
                  avatar: '',
                  date: '17 June 2021',
                  timestamp: '10:20',
                  disableReactions: true
            }
            if(file){
                message.file = {
                    name: file.name,
                    size: file.size,
                    type: file.type,
                    extension: file.extension || file.type,
                    url: file.localUrl
                };
                if(file.audio){
                    message.file.audio = true;
                    message.file.duration = file.duration;
                }
            }
                
            if(replyMessage)
                message.replyMessage = {
                    _id: replyMessage._id,
                    content: replyMessage.content,
                    sender_id: replyMessage.senderId
                }
            const res = await this.sendMessage({msg: message.content, user: 26140});
            console.log(res);
            this.messages.push(message);
        },
        typingMessage: function({message, roomId}){
        },
        deleteMessageAction: function({roomId, message}){
            this.messages = this.messages.filter(x=>x._id != message._id);
        },
        selectUser: function(user){
            this.selectedUser = user;
        }
    }
}
</script>

<style scoped>
    .row + .row {
        margin-top: 0px;
    }
</style>