<script>
import { mapGetters, mapActions } from 'vuex';
import Status from '../components/Status.vue';
import TheCalendar from '../components/TheCalendar.vue';
export default {
  name: 'Dashboard',
  components: { TheCalendar, Status },
  data: () => ({
    isLoaded: false,
    dialog: false,
    colors: [
