    ],
    tasks: [
          {
            title: 'Job 1',
            toWhom: 'Vugar',
          },
          {
            title: 'Job 2',
            toWhom: 'Seba',
    },],
    vacancyDialog: false,
    vacancyTitle: '',
    vacancyDialogData: ''
  }),
  computed: {
    ...mapGetters('hr', [
      'vacancies'
    ]),
    computedVacancies: function(){
      return this.vacancies.map((x) => { return {id: x.GUID, data: JSON.parse(x.EXTENDEDDATA)} });
    }
  },
  methods: {
    showVacancyDialog: function(data){
      this.vacancyDialog = true;
      this.vacancyTitle = data.Headline;
      this.vacancyDialogData = data.Story;
    }
  },
  created: function() {
    setTimeout(()=>{
        this.isLoaded = true;
    }, 500)
  }
}
</script>

